.overlay {
  max-width: 100%;
  margin: 70px 0;
  padding: 60px 25px;
  background-color: whitesmoke;
  font-family: Arial;
  font-size: 16px;
  line-height: 1.5rem;
  letter-spacing: 1px;
}

.modal-wrapper {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: inline-block;
  justify-content: center;
  padding: 50px 20px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  z-index: 16;
}

.popup {
  border-radius: 8px;
  font-family: Montserrat-Regular;
  position: relative;
  margin-bottom: 100px;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  height: -moz-fit-content;
  width: 400px;
  background-color: whitesmoke;
}

@media only screen and (max-width: 600px) {
  .popup {
    width: 300px;
  }
}

.btnFacebook {
  margin-top: 15px;
  width: 100%;
  height: 50px;
  border-radius: 4px;
  background: #3b5998;
  color: white;
  border: 0px transparent;
  text-align: center;
  display: inline-block;
  font-size: small;
  outline: "none";
}

.btnGoogle {
  margin-top: 10px;
  margin-bottom: 15px;
  width: 100%;
  height: 50px;
  border-radius: 4px;
  background: #db3236;
  color: white;
  border: 0px transparent;
  text-align: center;
  outline: "none";
}

.error {
  font-size: small;
}

.password {
  border-radius: 0.25rem;
  background-color: #fff;
}
